// -----------------------------------------------------------------------------


@font-face {
	font-family: 'Yu Mincho';
	font-weight: 100;
	src: local('Yu Mincho Regular'), local('YuMincho-Regular');
}

@font-face {
	font-family: 'Yu Mincho';
	font-weight: 200;
	src: local('Yu Mincho Regular'), local('YuMincho-Regular');
}

@font-face {
	font-family: 'Yu Mincho';
	font-weight: 300;
